import { getSheetData } from "../../lib/fetchGoogleSheet";
import Header from "@/app/components/Header";
import Footer from "@/app/components/Footer";
import { notFound } from "next/navigation";

export const revalidate = 600; // ISR every 10 mins

export default async function ProjectDetailPage({ params }: { params: { slug: string } }) {
  const { slug } = params;

  // ‚úÖ Fetch all projects
  const projectRows = await getSheetData("ProjectList");

  // ‚úÖ Find matching project
  const project = projectRows.find((p: any) => p.Slug === slug);

  if (!project) {
    notFound();
  }

  const gallery = (project.GalleryImages || "").split(",").map((img: string) => img.trim()).filter(Boolean);
  const highlights = (project.Highlights || "").split(",").map((h: string) => h.trim());
  const challenges = (project.Challenges || "").split(/[;,.]/).map((c: string) => c.trim()).filter(Boolean);
  const solutions = (project.Solutions || "").split(/[;,.]/).map((s: string) => s.trim()).filter(Boolean);
  const outcomes = (project.Outcomes || "").split(/[;,.]/).map((o: string) => o.trim()).filter(Boolean);

  return (
    <>
      <Header />
      <main className="bg-slate-50 min-h-screen">
        {/* HERO SECTION */}
        <section className="relative h-[400px] overflow-hidden flex items-center">
          <img
            src={`/img/${project.HeroImage}`}
            alt={project.Title}
            className="absolute inset-0 w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-r from-slate-900/80 via-slate-900/50 to-transparent" />
          <div className="container mx-auto relative z-10 px-6">
            <h1 className="text-4xl font-bold text-white mb-2">{project.Title}</h1>
            <p className="text-slate-200 text-lg max-w-2xl">{project.Description}</p>
            <div className="flex flex-wrap gap-4 mt-4 text-slate-200 text-sm">
              {project.Location && <span>üìç {project.Location}</span>}
              {project.Client && <span>üë∑ {project.Client}</span>}
              {project.Duration && <span>‚è±Ô∏è {project.Duration}</span>}
            </div>
          </div>
        </section>

        {/* DETAILS */}
        <section className="container mx-auto px-6 py-10">
          <div className="grid md:grid-cols-3 gap-10">
            {/* LEFT COLUMN */}
            <div className="md:col-span-2 space-y-8">
              {/* Gallery */}
              {gallery.length > 0 && (
                <div>
                  <h2 className="text-2xl font-semibold mb-4">Project Gallery</h2>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                    {gallery.map((img) => (
                      <img
                        key={img}
                        src={`/img/${img}`}
                        alt={project.Title}
                        className="rounded-xl object-cover w-full h-40 shadow"
                      />
                    ))}
                  </div>
                </div>
              )}

              {/* Challenges */}
              {challenges.length > 0 && (
                <div>
                  <h2 className="text-2xl font-semibold mb-3">Challenges</h2>
                  <ul className="list-disc pl-6 space-y-1 text-slate-700">
                    {challenges.map((c, i) => <li key={i}>{c}</li>)}
                  </ul>
                </div>
              )}

              {/* Solutions */}
              {solutions.length > 0 && (
                <div>
                  <h2 className="text-2xl font-semibold mb-3">Solutions</h2>
                  <ul className="list-disc pl-6 space-y-1 text-slate-700">
                    {solutions.map((s, i) => <li key={i}>{s}</li>)}
                  </ul>
                </div>
              )}

              {/* Outcomes */}
              {outcomes.length > 0 && (
                <div>
                  <h2 className="text-2xl font-semibold mb-3">Outcomes</h2>
                  <ul className="list-disc pl-6 space-y-1 text-slate-700">
                    {outcomes.map((o, i) => <li key={i}>{o}</li>)}
                  </ul>
                </div>
              )}
            </div>

            {/* RIGHT COLUMN */}
            <aside className="space-y-6">
              <div className="bg-white p-6 rounded-xl shadow">
                <h3 className="font-semibold text-lg mb-3">Project Info</h3>
                <ul className="text-slate-700 text-sm space-y-2">
                  <li><b>Client:</b> {project.Client}</li>
                  <li><b>Location:</b> {project.Location}</li>
                  <li><b>Duration:</b> {project.Duration}</li>
                  {project.Size && <li><b>Size:</b> {project.Size}</li>}
                  {project.Budget && <li><b>Budget:</b> {project.Budget}</li>}
                </ul>
              </div>

              <div className="bg-white p-6 rounded-xl shadow">
                <h3 className="font-semibold text-lg mb-3">Highlights</h3>
                <ul className="list-disc pl-6 space-y-1 text-slate-700">
                  {highlights.map((h, i) => <li key={i}>{h}</li>)}
                </ul>
              </div>
            </aside>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
}
